import { useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { Dashicon, PanelBody, RangeControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import Background from '../../Components/Background';
import SpaceControl from '../../Components/SpaceControl';

import icons from './Const/icons';
import options from './Const/options';
import spaceOffice from './img/open-space-office.jpg';
const { pxUnit, perUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes }) => {
	const { speed, verticalAlign, textAlign, background, minHeight, padding } = attributes;

	useEffect(() => {
		!background?.image?.url && setAttributes({
			background: { ...background, image: { url: spaceOffice } }
		});
	}, [background]);

	return <>
		<InspectorControls>
			<PanelBody className='bPlPanelBody help' title={__('Help', 'parallax-section')}>
				<div className='helpItem'>
					<a href='https://bblockswp.com/docs/parallax-section-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'parallax-section')}</a>
				</div>

				<div className='helpItem rateUs'>
					<a href='https://wordpress.org/support/plugin/parallax-section/reviews/#new-post' target='_blank' rel='noreferrer'>
						<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'parallax-section')}</span>
						<span>{__('We are new and we need your help to grow!🙏', 'parallax-section')}</span>
					</a>
				</div>
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('Parallax Settings', 'parallax-section')}>
				<Title mt='0'>{__('Speed:', 'parallax-section')}</Title>
				<RangeControl value={speed} onChange={val => setAttributes({ speed: val })} min={-1} max={1} step={.01} />
				<small>{__('0 will not perform parallax', 'parallax-section')}</small>

				<Background className='mt20' label={__('Background', 'parallax-section')} background={background} onChange={val => setAttributes({ background: val })} defaults={{ type: 'image' }} isSolid={false} />

				<UnitControl className='mt20' label={__('Min Height:', 'parallax-section')} labelPosition='left' value={minHeight} onChange={val => setAttributes({ minHeight: val })} units={[pxUnit, perUnit, emUnit]} />

				<SpaceControl className='mt20' label={__('Padding:', 'parallax-section')} space={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '20px', horizontal: '30px' }} />
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={verticalAlign} onChange={val => setAttributes({ verticalAlign: val })} describedBy={__('Vertical Alignment')} alignmentControls={[
				{ title: __('Content in top', 'parallax-settings'), align: 'flex-start', icon: icons.verticalTop },
				{ title: __('Content in center', 'parallax-settings'), align: 'center', icon: icons.verticalCenter },
				{ title: __('Content in bottom', 'parallax-settings'), align: 'flex-end', icon: icons.verticalBottom }
			]} />

			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;