import { useEffect, useRef } from '@wordpress/element';
import { InnerBlocks } from '@wordpress/block-editor';

import Settings from './Settings';
import Style from './Style';
import { parallaxInit } from './Const/functions';

const Edit = props => {
	const { className, attributes, setAttributes, clientId } = props;
	const { speed } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	const parallaxImgEl = useRef(null);

	const visualEditorEl = document.querySelector('.edit-post-visual-editor')?.parentElement;
	useEffect(() => {
		const thisBlock = document.getElementById(`block-${clientId}`);
		parallaxInit(parallaxImgEl.current, thisBlock, visualEditorEl.scrollTop + visualEditorEl.offsetTop);

		visualEditorEl.addEventListener('scroll', () => {
			parallaxInit(parallaxImgEl.current, thisBlock, visualEditorEl.scrollTop + visualEditorEl.offsetTop);
		});
	}, [parallaxImgEl, speed]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} />

		<div className={className} id={`psbParallaxSection-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<div className='psbParallaxImg' data-speed={speed} ref={parallaxImgEl}></div>

			<div className='psbParallaxSection'>
				<InnerBlocks template={[
					['core/heading', {
						content: 'Parallax Section by bPlugins',
						style: {
							typography: { fontSize: '40px' },
							color: { text: '#fff' }
						}
					}],

					['core/paragraph', {
						content: 'Parallax effect is a modern web design technique where background element scrolls slower than foreground content. Parallax effect can be used on landing pages, long-form content, sales pages, or homepage of a business website. It is a great way to highlight different sections on a lengthy page.',
						style: {
							typography: { fontSize: '20px' },
							color: { text: '#fff' }
						}
					}],

					['core/buttons', { contentJustification: 'center', layout: { justifyContent: 'center' } }, [
						['core/button', {
							text: 'Click Me',
							style: {
								typography: { fontSize: '30px' },
								color: { text: '#fff', gradient: 'linear-gradient(135deg, #4527a4, #8344c5)' }
							}
						}]
					]]
				]} />
			</div>
		</div >
	</>;
};
export default Edit;